@font-face {
  font-family: 'Linearicons';
  src:  url('fonts/Linearicons.eot?kr0kvi');
  src:  url('fonts/Linearicons.eot?kr0kvi#iefix') format('embedded-opentype'),
    url('fonts/Linearicons.ttf?kr0kvi') format('truetype'),
    url('fonts/Linearicons.woff?kr0kvi') format('woff'),
    url('fonts/Linearicons.svg?kr0kvi#Linearicons') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="lnr-"], [class*=" lnr-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Linearicons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#container {
  margin: 20px;
  width: 800px;
  border: 1px solid #ECECEC;
  display: -ms-flexbox;
  display: flex;
  background-color: #f9f9f9;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
          box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

#left {
  width: 20%;
  display: inline-block;
  background-color: #fff;
  border-right: 1px solid #ECECEC;
}

#right {
  width: 80%;
  display: inline-block;
}

#messages-list {
  padding: 0 20px;
  height: 500px;
  overflow: scroll;
}

#new-message {
  border-top: 1px solid #E6E6E6;
  height: 80px;
  background-color: #fff;
}

#new-message > input {
  width: 625px;
  padding: 18px 0 43px 13px;
  border: none;
  font-size: 14px;
}

#channels ul {
  padding: 0;
  margin: 0;
}

#channels ul p {
  border-bottom: 1px solid #E6E6E6;
  padding: 30px 20px;
  text-align: center;
  cursor: pointer;
  margin: 0;
}

ul {
  padding: 0;
}

#message {
  padding: 6px 16px 6px 0;
}

#message span.title {
  font-weight: 500;
  color: #555;
  margin-right: 4px;
  font-size: 15px;
}

#message p {
  margin: 0;
}

#message span.message-bubble {
  display: inline-block;
  border-radius: 4px;
  padding: 6px 16px;
  margin-top: 3px;
  color: #404040;
  -webkit-text-align: left;
  text-align: left;
  max-width: 60%;
  border: 1px solid rgb(218, 218, 218);
  background-color: rgb(241, 241, 241);
  margin-left: 3px;
}


